$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
@import 'nouislider/dist/nouislider.css';

$className: 'range-slider';

$noUiSlider: 'noUi';

.#{$className} {
  max-width: $medium;

  &__label {
    margin-bottom: toRem(23);
  }

  &__slider {
    max-height: $range-slider-height;
    background-color: white;
    border-color: $dark-shade-25;
    box-shadow: none;

    .#{$noUiSlider} {
      &-connects {
        overflow: initial;
      }

      &-connect {
        background-image: $secondary-gradient;
        height: $range-slider-height;
        top: toRem(-1);
      }

      &-handle {
        box-shadow: none;
        max-height: toRem(12);
        max-width: toRem(12);
        background-image: $secondary-gradient;
        top: toRem(-6);
        right: toRem(-8);
        cursor: pointer;
        box-sizing: content-box;
        border-radius: 50%;
        border: toRem(2) solid white;

        &::before, &::after {
          display: none;
        }
      }
    }
  }

  &__description {
    font-size: toRem(12);
    line-height: toRem(14);
    color: $dark-shade-50;
    margin-top: toRem(13);
  }
}
