$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'form-elements';

.#{$className} {
  margin-top: toRem(66);

  &__main-elements {
    display: flex;
    justify-content: space-between;
  }

  &__column-left,
  &__column-right {
    min-width: $big;
  }

  &__column-middle {
    min-width: $medium;
  }

  &__input-field-hovered,
  &__input-field-masked {
    margin-top: toRem(23);
  }

  &__dropdown-default {
    margin-top: toRem(21);
  }

  &__date-dropdown-double,
  &__date-dropdown-single,
  &__input-field-subscription {
    margin-top: toRem(42);
  }

  &__radio-buttons {
    margin-top: toRem(43);
  }

  &__label-wrapper {
    margin-bottom: toRem(13);
  }

  &__toggle-switches {
    margin-top: toRem(41);
  }

  &__toggle-switch-default {
    margin-top: toRem(10);
  }

  &__like-buttons {
    margin-top: toRem(43);
  }

  &__like-buttons-wrapper {
    display: flex;
    column-gap: toRem(10);
    margin-top: toRem(15);
  }

  &__rate-buttons {
    margin-top: toRem(41);
  }

  &__rate-buttons-wrapper {
    display: flex;
    column-gap: toRem(20);
    margin-top: toRem(16);
  }

  &__buttons {
    margin-top: toRem(44);
  }

  &__buttons-filled {
    margin-top: toRem(16);
  }

  &__buttons-bordered {
    margin-top: toRem(30);
  }

  &__buttons-default {
    margin-top: toRem(32);
  }

  &__button-with-icon {
    margin-top: toRem(29);
  }

  &__buttons-filled,
  &__buttons-bordered,
  &__buttons-default {
    display: flex;
    column-gap: toRem(20);
  }

  &__pagination {
    margin-top: toRem(41);
  }

  &__pagination-wrapper {
    margin-top: toRem(15);
  }

}