$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'checkbox';

.#{$className} {
  max-width: $medium;

  &__body {
    display: flex;
    align-items: flex-start;
    cursor: pointer;
  }

  &__checkbox-input {
    @include hideInput;
  }

  &__checkmark {
    position: relative;
    border-radius: toRem(4);
    border: toRem(1) solid $dark-shade-25;
    height: $checkbox-size;
    min-width: toRem(20);
    margin-right: $checkbox-margin;

    &::after {
      position: absolute;
      margin: auto;
      left: 0;
      right: 0;
      top: 0;
      bottom: 10%;
      content: '';
      height: toRem(8);
      width: toRem(6);
      border-style: solid;
      border-width: toRem(2);
      border-image: $primary-gradient 1;
      border-left: none;
      border-top: none;
      transform: rotate(45deg);
      box-sizing: border-box;
      display: none;
    }
  }

  &__checkbox-input:checked
  + &__checkmark {
    border-color: $purple;

    &::after {
      display: initial;
    }
  }

  &__title {
    color: $dark-shade-50;
    line-height: toRem(18);
    white-space: pre-line;
  }

  &__description {
    color: $dark-shade-50;
    font-size: toRem(12);
    line-height: toRem(14);
    padding-left: $checkbox-size + $checkbox-margin;
    margin-top: toRem(5);
    white-space: pre-line;
  }

  &_rich {
    .#{$className}__title {
      font-weight: 700;
      color: $dark-shade-75;
    }
  }
}